.hexagonal-grid {
    position: relative;
}

.game-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh; /* Ensure the container takes at least the full viewport height */
    padding: 20px 0;
}

.game-controls {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 20px;
}

.game-instructions {
    margin-top: 20px;
    padding: 20px;
    border-radius: 5px;
    width: 80%;
    text-align: center;
}

.score {
    margin: 10px;
    font-size: 20px;
    font-weight: bold;
}


button {
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    outline: none;
}

button:hover, button:focus {
    background-color: #0056b3;
    transform: scale(1.05);
}

.reset-button {
    background-color: #28a745;
}

.how-to-play-button {
    background-color: #17a2b8;
}

.reset-button:hover, .reset-button:focus {
    background-color: #218838;
}

.how-to-play-button:hover, .how-to-play-button:focus {
    background-color: #138496;
}
